import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import connect from '@vkontakte/vk-connect';

import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActionArea from '@material-ui/core/CardActionArea';
import Typography from '@material-ui/core/Typography';

import openSocket from 'socket.io-client';

const useStyles = makeStyles({
	card: {
		display: 'flex',
	},
	controls: {
		display: 'flex',
		alignItems: 'center',
	},
});


class Home extends React.Component {
	
	constructor(props) {
		super(props);
	
		this.state = {
			activePanel: 'main',
			popout: null
		};

		this.login = this.login.bind(this);
		this.componentDidMount = this.componentDidMount.bind(this);
		
		connect.send("VKWebAppInit", {});
	}
	
	componentDidMount () {
		this.setState({ popout: <ScreenSpinner /> });
		setTimeout(() => { this.login() }, 100);
	}
	
	login() {
		const socket = openSocket.connect('https://ih1705413.vds.myihor.ru:8080/', {secure: true});
		socket.on('connect', function () {
			socket.json.send({'method': 'login','url': window.location.search.replace('?','')});
			socket.on('message', function (msg) {
				if(msg.method === 'login'){
					if(msg.access){
						this.setState({ popout: null });
					}
				}
				console.log(msg);
			}.bind(this));
		}.bind(this));
	}

	render() {
		const classes = useStyles();

		return (
			<View popout={this.state.popout}>
				<View activePanel={this.state.activePanel}>
					<Panel id="main">
						<PanelHeader>VK App</PanelHeader>
						<Card className={classes.card}>
							<CardActionArea>
								<CardContent>
									<Typography gutterBottom variant="h5" component="h2">
										Lizard
									</Typography>
								</CardContent>
							</CardActionArea>
						</Card>
					</Panel>
				</View>
			</View>
		);
	}
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Home;
